import uuid from 'uuid';

//ADD EXPENSES
export const addExpense = ( 
    { 
        description = '', 
        note = '', 
        amount = 0, 
        createdAt = 0 
    } = {}
) => ({
type: 'ADD_EXPENSE',
expenses: {
    id: uuid(),
    description,
    note,
    amount,
    createdAt
}
});

//REMOVE EXPENSE
export const removeExpense = ({ id } = {}) => ({ 
type: 'REMOVE_EXPENSE',
id
})

// EDIT EXPENSE
export const editExpense = (id, updates) => ({
type: 'EDIT_EXPENSE',
id,
updates
});

